<template>
    <div class="Isdat-Distributing-Columns" v-if="ready">
        <div :style="column_style" v-for="(col, index) in columns" :key="index">
            <div class="Isdat-Column" style="height: max-content" v-for="(block, index) in col" :key="block.uuid">
                <component :is="component_mapping(block)"
                           :block="block"
                ></component>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Isdat-Distributing-Columns',
        props: {
            blocks: {},
            columns_count: {},
            component_mapping: {},
            gutter: {
                type: Number,
                default: 4,
            },
        },
        data() {
            return {
                ready: 0,
                columns: [],
            };
        },
        watch: {
            columns_count: {
                handler(val) {
                    this.init_with(val);
                },
                immediate: true,
            },
        },
        computed: {
            column_style() {
                return {
                    flex: `0 0 calc(99.99% * 1/${this.columns_count})`,
                }
            }
        },
        methods: {
            init_with(val) {
                this.ready = 0;
                if (val === 2) {
                    this.columns = [this.blocks.slice(0,4),this.blocks.slice(4)];
                    this.ready = 1;
                    return;
                }
                this.columns = this.blocks.reduce((out, block, index) => {
                    out[index % val].push(block);
                    return out;
                }, new Array(val).fill(1).map(_ => ([])));
                this.ready = 1;
            },
        },
    };
</script>
