import uuid from "uuid/v4";

const makeCategory = (name, index) => ({
    name: name,
    id: index,
    uuid: uuid(),
});

const array_with_ids_to_map = items => items.reduce((acc, item) => {
    acc[item.id] = item;
    return acc;
}, {});

const apply = (subject, func) => func(subject);

export const CATEGORIES = apply([
    "Portrait-témoignage",
    "Entretien-rencontre",
    "Documentation",
    "Relevé-dessin",
    "Essai-recherche",
    "Journée d'étude",
    "Projet-étude"].map(makeCategory), array_with_ids_to_map);

export const get_category_from_string = str => Object.values(CATEGORIES).find(a => a.name === str);

export const CAT_PORTRAIT = get_category_from_string("Portrait-témoignage");
export const CAT_ENTRETIEN = get_category_from_string("Entretien-rencontre");
export const CAT_DOCUMENTATION = get_category_from_string("Documentation");
export const CAT_RELEVE = get_category_from_string("Relevé-dessin");
export const CAT_ESSAI = get_category_from_string("Essai-recherche");
export const CAT_JOURNEE = get_category_from_string("Journée d'étude");
export const CAT_PROJET = get_category_from_string("Projet-étude");

export const BLOCK_TYPES = apply([
    "Square",
    "Circle",
    "Hex",
    "Trapezoid",
    "Empty",
].map(makeCategory), array_with_ids_to_map);

export const get_block_type_from_string = str => Object.values(BLOCK_TYPES).find(a => a.name === str);

export const TYPE_SQUARE = get_block_type_from_string("Square");
export const TYPE_CIRCLE = get_block_type_from_string("Circle");
export const TYPE_HEX = get_block_type_from_string("Hex");
export const TYPE_TRAPEZOID = get_block_type_from_string("Trapezoid");
export const TYPE_EMPTY = get_block_type_from_string("Empty");


const CAT_TO_BLOCK = {
    "Portrait-témoignage": "Square",
    "Entretien-rencontre": "Square",
    "Documentation": "Circle",
    "Relevé-dessin": "Circle",
    "Essai-recherche": "Circle",
    "Journée d'étude": "Hex",
    "Projet-étude": "Trapezoid",
    "Empty": null
};

export const COLORS = {
    WHITE: 'rgb(255,255,255)',
    GREEN: 'rgb(140,137,56)',
    BLUE: 'rgb(85,103,214)',
    YELLOW: 'rgb(255,240,132)',
};

export const COL_WHITE = 'WHITE';
export const COL_BLUE = 'BLUE';
export const COL_GREEN = 'GREEN';
export const COL_YELLOW = 'YELLOW';

export const get_color_value = (color) => {
    return COLORS[color];
};

let id = 1;
const make_block = (name, type, category, color = 'white', lines, link) => {
    const b = {
        id,
        uuid: uuid(),
        type,
        category,
        name,
        color: get_color_value(color),
        color_name: color,
    };
    if (lines) {
        b.lines = lines;
    }
    if (link) {
        b.link = link;
    }
    id += 1;
    return b;
};

