import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const MUTATIONS = {
    SET_WINDOW_WIDTH: "setWindowWidth",
    SET_MENU_STATE: 'setMenuState',
    SET_TEXT_HEIGHT: 'setTextHeight',
};

export default new Vuex.Store({
    state: {
        window_width: window && window.innerWidth || 0,
        menu_open: false,
        text_height: 24,
    },
    mutations: {
        [MUTATIONS.SET_WINDOW_WIDTH](s, _) {
            s.window_width = window && window.innerWidth || 0;
        },
        [MUTATIONS.SET_MENU_STATE](s, val) {
            s.menu_open = val;
        },
        [MUTATIONS.SET_TEXT_HEIGHT](s, val) {
            s.text_height = val;
        }
    },
    actions: {},
    getters: {
        window_width: s => s.window_width,
        column_count: s => {
            if (s.window_width < 640) {
                return 1;
            }
            if (s.window_width < 960) {
                return 2;
            }
            return 3;
        }
    },
    modules: {}
})
