export const BASE_HEIGHT = 95;

export const BLOCK_MIXIN = {
    data: function () {
        return {
            lines: 1,
            animate: true,
            height: `${BASE_HEIGHT}px`,
            adder: 1,
        };
    },
    props: {
        block: {},
    },
    watch: {
        num(val) {
            this.animate = true;
            setTimeout(() => {
                this.animate = false;
            }, 300);
            this.do_adder();
        },
        text_height(val) {
            this.set_lines();
            this.do_adder();
        },
        $route(val) {
            setTimeout(() => {
                this.set_lines();
                this.do_adder();
            }, 0);
        },
    },
    computed: {
        text_height() {
            return this.$store.state.text_height;
        },
        num() {
            return this.$store.getters.column_count;
        },
        category_class: function () {
            return this.block && this.block.category && this.block.category.name.replace(' ', '-') || "";
        }
    },
    methods: {
        set_height() {
            if (this.lines === 0) {
                this.height = `${BASE_HEIGHT}px`;
                return;
            }
            if (this.lines === 1) {
                this.height = `${BASE_HEIGHT * 1.5}px`;
                return;
            }
            this.height = `${BASE_HEIGHT * Math.pow(1.5, this.lines) + this.adder}px`;
        },
        do_adder() {
            this.adder += 1;
            this.$nextTick(() => {
                this.adder = 0;
            })
        },
        set_lines: function () {
            if (this.block && this.block.lines) {
                this.height = 'auto';
                this.lines = this.block.lines;
                this.set_height();
                return;
            }
            const block_measure = this.$el.querySelector('.block_measure span');
            if (!block_measure) return;
            this.height = 'auto';
            this.$nextTick(() => {
                const h = block_measure.getBoundingClientRect().height;
                this.lines = parseInt(h / this.$store.state.text_height, 10);
                this.set_height();
            });
        },
    },
    mounted: function () {
        this.set_lines();
        this.animate = true;
        setTimeout(() => {
            this.animate = false;
        }, 300);
        this.$root.$on('resize', () => {
            this.do_adder();
        });
    },
};
