<template>
    <div id="nav" class="isdat-nav" :class="{ open: open || original_open}">
        <div class="isdat-nav-left">

        </div>
        <div class="isdat-nav-center">
            <h1 @click="close_menu">
                <router-link :to="'/'" style="text-transform: uppercase; letter-spacing: 5px;">Penser faire &amp; faire penser</router-link>
            </h1>
        </div>
        <div class="isdat-nav-right">
            <isdat-burger/>
        </div>
    </div>
</template>

<script>
    import IsdatBurger from "./IsdatBurger";
    import {MUTATIONS} from "./store";

    export default {
        name: 'isdat-nav',
        data() {
            return {
                open: true,
                scroll_value: 0,
            };
        },
        components: {IsdatBurger},
        computed: {
            original_open() {
                return this.$store.state.menu_open;
            },
        },
        methods: {
            close_menu() {
              this.$store.commit(MUTATIONS.SET_MENU_STATE, false);
            },
            scrollHandler(val) {
                if (val < 120) {
                    this.open = true;
                    this.scroll_value = val;
                    return;
                }
                if (val < this.scroll_value) {
                    this.open = true;
                } else {
                    this.open = false;
                }
                this.scroll_value = val;
            },
        },
        mounted() {
            window.addEventListener('scroll', () => {
                this.scrollHandler(window.scrollY);
            });
        },
    }
</script>
