<template>
    <button class="isdat-burger hamburger hamburger--squeeze" @click="open = !open" :class="{ 'is-active' : open }" type="button"
            aria-label="Menu" aria-controls="navigation">
  <span class="hamburger-box">
    <span class="hamburger-inner"></span>
  </span>
    </button>
</template>
<script>
    import {MUTATIONS} from "./store";

    export default {
        name: 'isdat-burger',
        computed: {
            open: {
                get() {
                    return this.$store.state.menu_open;
                },
                set(v) {
                    this.$store.commit(MUTATIONS.SET_MENU_STATE, v);
                },
            }
        }
    }
</script>
