import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import store, {MUTATIONS} from './../store';

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Accueil',
        component: Home
    },
    {
        path: '/presentation',
        name: 'Présentation',
        component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    },
    {
        path: '/cycle/1:habitat-et-precarite',
        name: 'Cycle 1 : Habitat et précarité',
        component: () => import(/* webpackChunkName: "habitat-precarite" */ '../views/CycleOne.vue')
    },
    {
        path: '/cycle/2:travailler-pour-nous-a-caylus',
        name: 'Cycle 2 : Travailler pour nous à Caylus',
        component: () => import(/* webpackChunkName: "travailler-caylus" */ '../views/CycleTwo.vue')
    },
    {
        path: '/cycle/3:design-et-supermarches-cooperatifs',
        name: 'Cycle 3 : Design et supermarchés coopératifs',
        component: () => import(/* webpackChunkName: "design-supermarches" */ '../views/CycleThree.vue')
    }
]

const router = new VueRouter({
    routes,
})

router.afterEach(() => {
   store.commit(MUTATIONS.SET_MENU_STATE, false);
   if (window) {
       window.scrollTo(0, 0);
   }
});

export default router
