<template>
    <div class="home">
        <Isdat-Distributing-Columns :blocks="blocks"
                                    :columns_count="columns_count"
                                    :component_mapping="component_mapping">
        </Isdat-Distributing-Columns>
    </div>
</template>

<script>
    import IsdatDistributingColumns from "../components/IsdatDistributingColumns";
    import {SAMPLE_SITE_BLOCKS, TYPE_CIRCLE, TYPE_EMPTY, TYPE_HEX, TYPE_SQUARE, TYPE_TRAPEZOID} from "../lib/blocks";
    import BlockSQUARE from "../components/blocks/BlockSQUARE";
    import BlockCIRCLE from "../components/blocks/BlockCIRCLE";
    import BlockHEX from "../components/blocks/BlockHEX";
    import BlockTRAPEZOID from "../components/blocks/BlockTRAPEZOID";
    import BlockEMPTY from "../components/blocks/BlockEMPTY";

    export default {
        name: 'Home',
        components: {
            IsdatDistributingColumns,
            BlockSQUARE,
            BlockCIRCLE,
            BlockHEX,
            BlockTRAPEZOID,
            BlockEMPTY
        },
        computed: {
            blocks() {
                return SAMPLE_SITE_BLOCKS;
            },
            columns_count() {
                return this.$store.getters.column_count;
            },
            component_mapping() {
                return block => ({
                    [TYPE_CIRCLE.name]: BlockCIRCLE,
                    [TYPE_SQUARE.name]: BlockSQUARE,
                    [TYPE_EMPTY.name]: BlockEMPTY,
                    [TYPE_HEX.name]: BlockHEX,
                    [TYPE_TRAPEZOID.name]: BlockTRAPEZOID
                }[block.type.name] || BlockEMPTY);
            },
        },
    };
</script>
