<template>
  <div id="app">
    <isdat-nav />
    <div class="intro bordered" style="margin-top: 90px">
      <div class="inside">
        Cette plate-forme de publication est phrase d'introduction sur la
        plate-forme. <a href="/about">En savoir plus</a>
      </div>
    </div>
    <div class="intro">
      <div class="inside">Filtrer les articles</div>
    </div>
    <div class="bordered" style="padding-bottom: 15px;">
      <div class="filters">
        <div
          class="filter-trigger"
          :class="{ active: show_programs }"
          @click="show_programs = !show_programs"
        >
          Programmes
        </div>
        <div
          class="program"
          :class="{ active: selected_programs.indexOf(p.id) !== -1 }"
          :key="'p-' + p.id"
          @click="toggle_in('selected_programs', p.id)"
          v-for="p in show_programs
            ? []
            : programs.filter((t) => selected_programs.indexOf(t.id) !== -1)"
        >
          {{ p.name }}
        </div>
        <div
          class="program"
          :class="{ active: selected_programs.indexOf(p.id) !== -1 }"
          :key="'p-' + p.id"
          @click="toggle_in('selected_programs', p.id)"
          v-for="p in available_programs"
        >
          {{ p.name }}
        </div>

        <div
          class="filter-trigger"
          :class="{ active: show_cycles }"
          @click="show_cycles = !show_cycles"
        >
          Cycles
        </div>
        <div
          class="cycles"
          :class="{
            active: selected_cycles.indexOf(p.id) !== -1,
            disabled: is_disabled_cycle(p),
          }"
          :key="'t-' + p.id"
          @click="toggle_in('selected_cycles', p.id)"
          v-for="p in show_cycles
            ? []
            : cycles.filter((t) => selected_cycles.indexOf(t.id) !== -1)"
        >
          {{ p.name }}
        </div>
        <div
          class="cycles"
          :class="{ active: selected_cycles.indexOf(p.id) !== -1, disabled: is_disabled_cycle(p) }"
          :key="'c-' + p.id"
          @click="toggle_in('selected_cycles', p.id)"
          v-for="p in available_cycles"
        >
          {{ p.name }}
        </div>

        <div
          class="filter-trigger"
          :class="{ active: show_typologies }"
          @click="show_typologies = !show_typologies"
        >
          Typologies
        </div>
        <div
          class="typologies"
          :class="{
            active: selected_typologies.indexOf(p.id) !== -1,
            [`typologie-g-${get_group(p).id}`]: 1,
          }"
          :key="'t-' + p.id"
          @click="toggle_in('selected_typologies', p.id)"
          v-for="p in show_typologies
            ? []
            : typologies.filter(
                (t) => selected_typologies.indexOf(t.id) !== -1
              )"
        >
          {{ p.name }}
        </div>
        <div
          class="typologies"
          :class="{
            active: selected_typologies.indexOf(p.id) !== -1,
            [`typologie-g-${get_group(p).id}`]: 1,
          }"
          :key="'t-' + p.id"
          @click="toggle_in('selected_typologies', p.id)"
          v-for="p in available_typologies"
        >
          {{ p.name }}
        </div>
        <div class="filter-trigger">Périodes</div>
        <div class="filter-trigger">Mots-clés</div>
      </div>
    </div>
    <div class="fake_page"></div>
  </div>
</template>

<script>
import IsdatNav from "./IsdatNav";
import { MUTATIONS } from "./store";

export default {
  components: { IsdatNav },
  data() {
    return {
      show_programs: false,
      show_cycles: false,
      show_typologies: false,
      available_programs: [],
      available_cycles: [],
      available_typologies: [],
      selected_programs: [],
      selected_cycles: [],
      selected_typologies: [],
      programs: [
        { id: 1, name: "Global Tools" },
        { id: 2, name: "Design et Pauvreté" },
        { id: 3, name: "Changement d'échelle" },
      ],
      cycles: [
          { id: 4, program: 1, name: "Cycle Global Tools" },
        { id: 5, program: 1, name: "Cycle Global Tools" },
        { id: 6, program: 1, name: "Cycle Global Tools" },
        { id: 1, program: 2, name: "Travailler pour nous à Caylus" },
        { id: 2, program: 2, name: "Habitat et précarité" },
        { id: 3, program: 2, name: "Re-former le supermarché" },
        { id: 7, program: 3, name: "Cycle Changement d'échelle" },
        { id: 8, program: 3, name: "Cycle Changement d'échelle" },
        { id: 9, program: 3, name: "Cycle Changement d'échelle" },
      ],
      typologies: [
        { id: 1, name: "Ressource", group: 1 },
        { id: 2, name: "Entretien", group: 2 },
        { id: 3, name: "Rencontre", group: 2 },
        { id: 4, name: "Projet", group: 3 },
        { id: 5, name: "Étude", group: 3 },
        { id: 6, name: "Enquête", group: 3 },
        { id: 7, name: "Journée d'études", group: 4 },
        { id: 8, name: "Publications", group: 4 },
        { id: 9, name: "Expositions", group: 4 },
      ],
      typologie_groups: [
        { id: 1, bg: "#8CB8FF", fg: "#D24A41" },
        { id: 2, bg: "#FFEB32", fg: "#8C4D55" },
        { id: 3, bg: "#64327D", fg: "#FF8264" },
        { id: 4, bg: "#2F3BBD", fg: "#1ABE78" },
      ],
    };
  },
  methods: {
    fade_unfade(val, str) {
      if (val) {
        this.fade_chunks(str);
      } else {
        this.unfade_chunks(str);
      }
    },
    is_disabled_cycle(p) {
        if (this.selected_programs.length < 1) return false;
        return this.selected_programs.indexOf(p.program) === -1;
    },
    fade_chunks(str) {
      this[str].forEach((p, i) => {
        setTimeout(() => {
          this[`available_${str}`].push(p);
        }, i * 100);
      });
    },
    unfade_chunks(str) {
      this[str].forEach((p, i) => {
        setTimeout(() => {
          this[`available_${str}`].splice([`available_${str}`].length - 1, 1);
        }, i * 50);
      });
    },
    get_group({ id: i, group: g }) {
      return this.typologie_groups.find((a) => a.id === g);
    },
    toggle_in(identifier, id) {
      if (this[identifier].indexOf(id) !== -1) {
        this[identifier] = this[identifier].filter((i) => i !== id);
      } else {
        this[identifier].push(id);
      }
    },
  },
  watch: {
    show_programs(val) {
      this.fade_unfade(val, "programs");
    },
    show_typologies(val) {
      this.fade_unfade(val, "typologies");
    },
    show_cycles(val) {
      this.fade_unfade(val, "cycles");
    },
  },
};
</script>

